  import   $ from 'jquery'

class Events {

  constructor () {
    this.listeners = {};
  }

  on (type, callback) {
    if (!this.listeners[type]) {
      this.listeners[type] = [];
    }
    this.listeners[type].push(callback);
  }

  off (type, callback) {
    if (this.listeners[type]) {
      this.listeners[type] = this.listeners[type].filter(listener => listener !== callback);
    }
  }

  emit (type, payload) {
    // return new Promise(resolve => {
      // setTimeout(t => {
        let res = true;
        (this.listeners[type] || []).forEach(listener => {
          if (listener(payload) === false) {
            res = false;
            // break?
          }
        });
        return res;
        // resolve(res);
      // }, 0);
    // });
  }
}


 
class Search extends Events {

  constructor ($container, endpoint) {
    super();

    this.endpoint = endpoint;

 

    this.$input = $container.find('.search-input');

    this.$input.keydown(e => {
      if (e.keyCode !== 13) {
        return;
      }
      if (this.$input.val()) {
        this.submit(this.$input.val());
        this.$input.blur(); // for iOS in order to close the keyboard
      }
      e.preventDefault();
    });

    this.$input.focus(e => {
      if (this.hasResults) {
        this.$results.addClass('visible');
      }
    });

    this.$button = $container.find('.search-button');
    this.$button.click(e => {
      if (this.$input.val()) {
        this.submit(this.$input.val());
      }
    });

    this.hasResults = false;
    this.$results = $container.find('.search-results');
  }

  read (json) {
    return json;
  }

  set value (value) {
    this.$input.val(value);
  }

  submit () {
    if (this.isLoading) {
      return;
    }

    const query = this.$input.val();
    if (!query) {
      return;
    }

    this.timeout = setTimeout(t => {
      if (!this.isLoading) {
        return;
      }
      this.showError('no results');
      this.emit('error', 'Timeout');
      this.isLoading = false;
      this.isTimeout = true;
    }, 5000);

    this.isLoading = true;
    this.hasResults = false;
    this.isTimeout = false;
    this.$results.removeClass('visible');

    this.emit('submit', query);
    Request.get(this.endpoint, { query: query }).then(results => {
      if (this.isTimeout) {
        return;
      }
      clearTimeout(this.timeout);
      results = this.read(results);
      this.showResults(results);
      this.emit('result', results);
      this.isLoading = false;
    }, err => {
      if (this.isTimeout) {
        return;
      }
      clearTimeout(this.timeout);
      this.showError('no results');
      this.emit('error', err);
      this.isLoading = false;
    });


  }  

  // set disabled (disabled) {
  //   this.$input.attr('disabled', disabled);
  //   this.$button.attr('disabled', disabled);
  // }

  showResults (geojson ) {
    if (!geojson || !geojson.features) {
      return this.showError('no results');
    }

    this.hasResults = true;
    this.$results.empty();

    geojson.features.forEach(feature => {
      this.renderItem(feature).appendTo(this.$results)
    });

    this.$results.addClass('visible');
    this.selectItem(geojson.features[0]);
  }

  showError (err = 'no results') {
    this.$results.html(`<div class="search-error">${err}</div>`);
    this.$results.addClass('visible');
  }

  hideResults () {
    this.$results.removeClass('visible');
  }

  renderItem (feature) {
    return $(`<div class="search-result-item" title="${feature.properties.name}">${feature.properties.name}</div>`)
      .click(e => {
        this.selectItem(feature);
      });
  }

  selectItem (feature) {
    this.value = feature.properties.name;
    this.$results.removeClass('visible');
    this.emit('select', feature);
  }
}

export default   Search ;
