<script>
import $ from 'jquery'
import mapboxgl from "mapbox-gl";
import style from '../assets/style.json';
import config from '../assets/js/config.js';
import params from "../assets/js/Params.js"
import Bounds from "../assets/js/bounds.js"
import Search from "../assets/search/search.js"

import indexmin from "../../public/js/index.min.js"
import       WA from '../../public/js/WA.js';
import       anime from '../../public/js/anime.min.js';

export default {
    data() {
        return {


            selectionColor: [186, 225, 207],
            searchEndpoint: `${config.endpoint}/search/?query={query}`,
            statsEndpoint: `${config.endpoint}/data/pricing/?region={region}`,
            maxWidthMobile: 760,
            selectionStyle: {
                lineColor: `rgba(${this.selectionColor}, 1)`,
                fillColor: `rgba(${this.selectionColor}, 0.2)`,
                handleColor: `rgba(${this.selectionColor}, 1)`
            }

        };
    },
    mounted() {
        this._showpageWelcome()
    setTimeout(this.init(), 30);




    },
    created() {
      

    },
    methods: {
        // initialization
        init() {

            // Loader
            this.loaderFn('visible');

            mapboxgl.accessToken = 'pk.eyJ1IjoiM2RidWlsZGluZ3MiLCJhIjoiY2syemRldWFvMDdueDNtcWpiNG8zajhhZSJ9.Zy2CUldC8U-RjcrAvtP6ZA';
            // initialize map
            this.map = new mapboxgl.Map({
                container: "map",
                style: style,
                center: [113.32072, 23.11804],
                zoom: 14.61,
                bearing: 0,
                pitch: 59,
                //attributionControl: true,
                hash: true,
                antialias: false
            });

            var that = this;

            this.map.on('load', function() {
                that.loaderFn('hidden');


                that.initDataTime();
                that.initXUI();
                that.initclikevent();
                that.initSearchevent();



                var toggleableLayerIds = ['building-footprint', 'building', "building-cluster", "building-point"];
                var link = $('#toglayer'); /* 创建a标签 */
                link.on('click', function(e) {
                    e.preventDefault();
                    e.stopPropagation();
                    for (var i = 0; i < toggleableLayerIds.length; i++) {
                        var id = toggleableLayerIds[i];
                        var clickedLayer = id;

                        var visibility = that.map.getLayoutProperty(clickedLayer, 'visibility');
                        if (visibility == undefined) {
                            visibility = "visible";
                        }

                        if (visibility === 'visible') {

                            that.map.setLayoutProperty(clickedLayer, 'visibility', 'none');
                            link.innerHTML = "3D"

                        } else {

                            link.innerHTML = "2D"
                            that.map.setLayoutProperty(clickedLayer, 'visibility', 'visible');
                        }

                    }

                });


            });


        },

        initSearchevent() {
            // initialize search
            const search = new Search($('#search'), this.searchEndpoint);
            search.on('submit', query => {

                params.query = encodeURIComponent(query);
                delete params.region;
                delete params.bbox;
                params.write();
            });

            search.on('select', feature => {
                const properties = feature.properties;
                const bounds = Bounds.fromGeometry(feature.geometry);
                if (!properties.stats) {
                    this.moveMapToPoint(bounds.center);
                    return;
                }

            });

            this.map.on('click', e => {
                search.hideResults();
            });

        },





        initclikevent() {
            var that = this;



            $('#map-button-zoomin').on('click', function(e) {
                that.map.zoomIn();
            })

            $('#map-button-zoomout').on('click', function(e) {
                that.map.zoomOut();
            })


            $('#maxbut').on('click', function(e) {
            
              WA.maxbut( )
              

            })

             $('#minbut').on('click', function(e) {
            
              WA.minbut( )

            })


             $('#closebut').on('click', function(e) {
            
              WA.closebut( )

            })



         



            $('#map-button-fullscreen').on('click', function(e) {

                if (e.currentTarget.className == "map-button-fullscreen") {
                    $('#map-button-fullscreen').removeClass("map-button-fullscreen")
                    $('#map-button-fullscreen').addClass("map-button-exitfullscreen")
                    that.requestFullscreen();
                } else {
                    $('#map-button-fullscreen').addClass("map-button-fullscreen")
                    $('#map-button-fullscreen').removeClass("map-button-exitfullscreen")
                    that.exitFullScreen();
                }

            });






        },

      


        initDataTime() {
            setInterval(() => { NowTime() }, 1e3);
            var NowTime = () => {
                var A = ["一", "二", "三", "四", "五", "六", "七", "八", "九", "十", "十一", "十二"];
                var g = new Date;
                var I = g.getFullYear();
                var C = g.getMonth();
                var e = g.getDate();
                var t = g.getHours();
                var r = g.getMinutes();
                var o = g.getSeconds();
                e = this.check(e);
                t = this.check(t);
                r = this.check(r);
                o = this.check(o);
                var a = t + " : " + r + " : " + o;


                $("#new-time").html(a);
                $("#new-month").html(A[Number(C)] + "月");
                $("#new-date").html(e);

                var today = new Date();
                var today_time = this.FormatDate(today);
                if (today_time > '2022-4-27') {
                    console.log('试用期已过,请重新申请！');
                   // return false;
                }




            };


        },

         _showpageWelcome() {

      var textWrapper = document.querySelector('.effect1');
      textWrapper.innerHTML = textWrapper.textContent.replace(/([^.\s]|\w)/g, "<span class='letter'>$&</span>");

      anime.timeline()
        .add({
          targets: '.effect1 .letter',
          scale: [3, 1],
          opacity: [0, 1],
          translateZ: 0,
          easing: "easeOutExpo",
          duration: 7150,
          delay: function(el, i) {
            return 70 * i;
          }
        }, 2000);

      var textWrapper2 = document.querySelector('.effect2');
      textWrapper2.innerHTML = textWrapper2.textContent.replace(/([^.\s]|\w)/g, "<span class='letter'>$&</span>");
      anime.timeline()
        .add({
          targets: '.effect2 .letter',
          scale: [3, 1],
          opacity: [0, 1],
          translateZ: 0,
          easing: "easeOutExpo",
          duration: 7150,
          delay: function(el, i) {
            return 70 * i;
          }
        }, 6000);
    },


        initXUI() {
            indexmin.InItfunction()

        },

        FormatDate(strTime) {
            var date = new Date(strTime);
            return date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
        },


        check(A) {

            var g = A < 10 ? "0" + A : A;
            return g
        },

        moveMapToPoint(point) {
            const lonPadding = 0.001 / Math.cos(point[1] * Math.PI / 180);
            const latPadding = 0.001;

            const bbox = {
                w: point[0] - lonPadding,
                s: point[1] - latPadding,
                e: point[0] + lonPadding,
                n: point[1] + latPadding
            };

            return this.moveMapToBBox(bbox);
        },

        moveMapToBBox(bbox) {
            let padding = {};
            if (innerWidth > this.maxWidthMobile) {
                padding = { left: 430, top: 50, right: 50, bottom: 50 }
            } else {
                padding = { left: 35, top: 35, right: 35, bottom: 70 }
            }

            const view = this.map.cameraForBounds([
                [bbox.w, bbox.s],
                [bbox.e, bbox.n]
            ], { padding });
            this.map.jumpTo({ ...view, bearing: 0, pitch: 0 });
        },

        requestFullscreen() {
            const docElm = document.documentElement
            if (docElm.requestFullscreen) {
                docElm.requestFullscreen()
            } else if (docElm.msRequestFullscreen) {
                docElm.msRequestFullscreen()
            } else if (docElm.mozRequestFullScreen) {
                docElm.mozRequestFullScreen()
            } else if (docElm.webkitRequestFullScreen) {
                docElm.webkitRequestFullScreen()
            }
        },


        exitFullScreen() {
            if (document.exitFullscreen) {
                document.exitFullscreen()
            } else if (document.msExitFullscreen) {
                document.msExitFullscreen()
            } else if (document.mozCancelFullScreen) {
                document.mozCancelFullScreen()
            } else if (document.webkitCancelFullScreen) {
                document.webkitCancelFullScreen()
            }
        },

        // Loader
        loaderFn(set) {
            const loader = $('.loader');
            if (set === 'visible') {
                loader.css("visibility", set);

            } else if (set === 'hidden') {

                       //loader.animate({"visibility": set}, 1000);

                      loader.animate({"opacity": "0"}, 8000);

                 setTimeout(() => {
                    loader.css("visibility", set);

                }, 8000); 
            }
        }

    },
};
</script>
